export default {
	props: {
		tabList: {
			type: Array,
			default: []
		},
		tabActiveIdx: {
			type: Number,
			default: 0
		}
	},
	data() {
		return {
			tabIdx: 0,
			scrollLeft: 0
		};
	},
	computed: {
		tabLen() {
			return this.tabList.length > 5 ? false : true;
		}
	},
	watch: {
		tabActiveIdx(newValue, oldValue) {
			this.tabSelect(newValue);
		}
	},
	methods: {
		tabSelect(item,idx) {
			if (this.timeout) {
				clearTimeout(this.timeout); //清除计时器				
			}
			this.timeout = setTimeout(() => {
				this.tabIdx = idx;
				this.$emit('tabSelect', {item,idx});
				this.scrollLeft = idx * 30;
			}, 300)
		},
		getId(id) {
			return `tab-${id}`
		}
	}
};
